export default {
	data() {
		return {
			navs: [{
					id: 1,
					name: "百问百答"
				},
				{
					id: 2,
					name: "视频专区"
				}
			],
			id: 1,
			viedos: [],
			prolist:'',
			curentId:'',
			scroll:false
		}
	},
	methods: {
		select(e) {
			this.id = e;
			this.prolist = '';
			console.log(e, "sajfksadjfka")
			service.auth(this , service.api.index.articleCate , 'GET' , {
				page:1,
				limit:15,
				type:e
			} , function(self , res){
				console.log(res)
				self.prolist = res.data;
				console.log(self.prolist)
			});
			this.$forceUpdate();
		},
		jump(e){
			uni.navigateTo({
				url:e
			});
		},
		jum(e){
			uni.switchTab({
				url:e
			});
		},
		PlayVideo(id){
			this.scroll = true
			this.curentId = id;
		}
		
	},
	onReady(res) {
		this.videoContext = uni.createVideoContext('myVideo');
	},
	onPageScroll(e){
		
		this.scroll = false
	},
	onShow() {
		service.auth(this , service.api.index.articleCate , 'GET' , {
			page:1,
			limit:15,
			type:1
		} , function(self , res){
			console.log(res)
			self.prolist = res.data;
			console.log(self.prolist)
		})
	}

}
